:root {
  --facebook-color: rgb(66, 103, 178);
  --max-width: 800px;
}

.all, .home {
  height: 100%;
  width: 100%;
}

.bar {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: calc(100% - 30px);
  padding: 15px;
  background: var(--facebook-color);
}

.bar .wrapper {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  max-width: var(--max-width);
}

.bar .wrapper .left {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.bar .wrapper .logo {
  height: 40px
}

.bar .pp {
  border-radius: 100%;
  height: 45px;
  width: 45px;
}

.bar .name {
  margin-left: 20px;
  font-size: large;
  font-weight: bold;
  color: white;
}

.list {
  padding: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.cover-item {
  width: calc(100% - 60px);
  background-color: white;
  padding: 15px 30px;
  display: flex;
  justify-content: space-between;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, .5);
  border-radius: 10px;
  align-items: center;
  max-width: calc(var(--max-width) - 60px);
}

.cover-item+.cover-item {
  margin-top: 15px;
}

.cover-item * {
  pointer-events: none;
}

.cover-item.ongoing {
  cursor: pointer;
}

.cover-item.closed {
  cursor: not-allowed;
}

.cover-item .info * {
  font-size: small;
}

.cover-item .name {
  font-size: large;
  font-weight: bold;
}

.cover-item .status {
  border: 2px solid gray;
  padding: 5px 15px;
  min-width: 70px;
  text-align: center;
  font-weight: bold;
  border-radius: 8px;
}

.cover-item.ongoing .status {
  border-color: green;
  color: green;
}

.cover-item.closed .status {
  border-color: darkred;
  color: darkred;
}

.cover-disp {
  width: calc(100% - 20px);
  padding: 10px;
}

.cover-disp.loading {
  position: fixed;
  height: 100%;
  width: 100%;
  background-color: rgba(255, 255, 255, .5);
  padding: 0;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

@keyframes rot {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(90deg);
  }
  100% {
    transform: rotate(180deg);
  }
}

.cover-disp.loading .loadanim {
  width: 170px;
  height: 170px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-size: x-large;
  font-weight: bold;
  color: var(--facebook-color);
}

.cover-disp.loading .loadanim::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: calc(100% - 20px);
  height: calc(100% - 20px);
  border: 10px solid var(--facebook-color);
  border-style: solid double;
  border-radius: 100%;
  animation: rot 1s ease-in-out infinite;
}

.cover-disp .disp-root .grid {
  width: 100%;
  margin: auto;
  max-width: var(--max-width);
  display: flex;
  flex-direction: column;
}

.cover-disp .disp-root .grid .row {
  width: 100%;
  display: flex;
  flex-direction: row;
}

.cover-disp .disp-root .grid .row+.row {
  margin-top: 10px;
}

.cover-disp .disp-root .grid .row .block {
  border-radius: 10%;
  position: relative;
  cursor: pointer;
}

.cover-disp .disp-root .grid .row .block img {
  border-radius: 10%;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.cover-disp .disp-root .grid .row .block.empty {
  background-color: rgb(210, 230, 250);
}

.cover-disp .disp-root .grid .row .block.empty::after {
  content: "";
  transition: all .3s;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: contain;
  transform: scale(.7);
  opacity: .6;
  background-image: url('add.svg');
}

.cover-disp .disp-root .grid .row .block.selected {
  background: white;
  box-shadow: 0 0 10px 0 var(--facebook-color);
  transform: scale(2);
  z-index: 10;
}

.cover-disp .disp-root .grid .row .block.selected::after {
  opacity: 0 !important;
}

.cover-disp .disp-root .grid .row .block.empty:hover {
  box-shadow: 0 0 10px 0 var(--facebook-color);
}

.cover-disp .disp-root .grid .row .block.empty:hover::after {
  transform: scale(1);
  opacity: 1;
}

.cover-disp .disp-root .grid .row .block+.block {
  margin-left: 10px;
}

.cover-disp .disp-root .pos-details {
  width: calc(100% - 30px);
  max-width: calc(var(--max-width) - 30px);
  margin: auto;
  min-height: 100px;
  border-radius: 15px;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, .5);
  margin-top: 40px;
  padding: 15px;
}

.pos-details .taken {
  display: flex;
  align-content: center;
}

.pos-details .empty {
  display: flex;
  align-content: center;
}

.pos-details .taken .actual-image {
  height: 120px;
  width: 120px;
  border-radius: 10px;
}

.pos-details .empty .actual-image {
  height: 116px;
  width: 116px;
  border-radius: 10px;
  border: 2px solid var(--facebook-color);
  position: relative;
}

.pos-details .empty .actual-image::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-image: url('empty.svg');
  background-size: cover;
  transform: scale(.7);
  opacity: .7;
}

.pos-details .taken .info {
  margin-left: 15px;
  width: calc(100% - 135px);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.pos-details .empty .info {
  margin-left: 15px;
  width: calc(100% - 135px);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.pos-details .taken .info .top {
  display: flex;
  align-items: center;
}

.pos-details .empty .info .top {
  font-size: large;
  font-weight: bold;
}

.pos-details .taken .info .top .profile-pic {
  height: 50px;
  width: 50px;
  border-radius: 10px;
}

.pos-details .taken .info .top .name {
  display: flex;
  flex-direction: column;
  margin-left: 15px;
}

.pos-details .taken .info .top .name .username {
  font-weight: bold;
}

.pos-details .taken .info .buttons {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
}

.pos-details .empty .info .buttons {
  font-size: small;
}

.pos-details .info .buttons .details-button {
  min-width: 60px;
  padding: 0 10px;
  height: 35px;
  display: flex;
  font-weight: bold;
  border-width: 2px;
  font-size: 16px;
  border-radius: 10px;
  border-style: solid;
  align-items: center;
  justify-content: space-around;
  cursor: pointer;
}

.pos-details .info .buttons .details-button.neutral {
  color: var(--facebook-color);
  border-color: var(--facebook-color);
}

.pos-details .info .buttons .details-button.neutral:hover {
  background-color: var(--facebook-color);
  color: white;
}

.pos-details .info .buttons .details-button.bad {
  color: darkred;
  border-color: darkred;
}

.pos-details .info .buttons .details-button.bad:hover {
  background-color: darkred;
  color: white;
}

.auth-cont {
  height: 100%;
  width: 100%;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: space-around;
  position: relative;
}

.auth-wrapper {
  position: relative;
}

.auth-wrapper .button-wrapper {
  overflow: hidden;
  border-radius: 15px;
  position: relative;
}

.auth-button {
  font-size: x-large;
  padding: 20px 30px;
  border-radius: 15px;
  font-weight: bold;
  background-color: transparent;
  cursor: pointer;
  position: relative;
  color: var(--facebook-color);
  border-color: var(--facebook-color);
  border-width: 3px;
  border-style: solid;
  z-index: 1;
}

.auth-cont .auth-wrapper .before {
  content: "";
  transition: all .3s;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0;
  background-color: var(--facebook-color);
  z-index: 0;
}

.auth-wrapper .logo {
  position: absolute;
  top: -100%;
  right: 0;
  left: 0;
  width: 150px;
  margin: auto;
  padding: 10px;
  border-bottom-right-radius: 15px;
  border-bottom-left-radius: 15px;
  background-color: black;
  pointer-events: none;
}

.auth-wrapper .logo-wrapper {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.button-wrapper:hover .auth-button {
  color: white;
}
.button-wrapper:hover .before {
  height: 100%;
}
.button-wrapper:hover+.logo-wrapper .logo {
  top: 0;
}